
import Vue from 'vue'
import { products } from '@/store'

export default Vue.extend({
  data() {
    return {
      product: {} as any,
      value: 'https://example.com',
      size: 300,
      test: false,
      page: 0,
    }
  },
  computed: {
    products() {
      return products
    },
  },
  methods: {
    async add(product: object) {
      const result = await products.add(product)
      if (result.message) {
        this.page = 1
      }
    },
    cancel() {
      this.test = true
    },
  },
})
